import { StyleSheet,Button, Text, TextInput, View } from "react-native";
import { useCallback, useState} from 'react';

//In order to enter, the login MUST be: Username:bob123 Password:Tom350


export default function Login({navigation,route}){   
   let [password,setPassword]=useState("")
   let [username,setUsername]=useState('')
   const [loggedin,setLoggedin]=useState(false)
   let [error,setError]=useState("")
   let signin=useCallback(()=>{
      console.log('update')
      if(password=="Tom350"){
         console.log('loggedin')

         setLoggedin(true)
      
         
      } else{
         console.log('false')
         setError("Wrong Password")

      }
   },[
      password
   ])
   
   return(
   
      (loggedin)?  
      <View style={styles.view}>
           <Text style={styles.text}> Welcome, {username}</Text>
           <Button onPress={()=>navigation.navigate("Home")} title="Log Out"></Button>
           <Button onPress={()=>navigation.navigate("Todo")} title="Login"></Button>


      </View >:
   <>
      <TextInput style={styles.input} onChangeText={(value)=>setUsername(value)} placeholder="Username"></TextInput> 
      <TextInput style={styles.input} onChangeText={(value)=>setPassword(value)} placeholder="Password"></TextInput> 
      <Text style={{color:'red'}}> {error}</Text>
      <Button title="Sign In" onPress={()=> signin(navigation.navigate("Todo"))}
        ></Button>


      </>
   );
}


const styles = StyleSheet.create({
   view:{
      justifyContent: 'center',

   },


   input:{
      height:40,
      margin: 12,
      borderWidth:1,
      padding:10,
      backgroundColor:'#ADD8E6',
      textAlign: "center"


   },
   text: {
      fontWeight: "bold",
      fontSize: "1.5rem",
      marginVertical: "1em",
      textAlign: "center"
   }
});