import { StyleSheet,Button, Text, TextInput, View } from "react-native";
import { useCallback, useState} from 'react'
import { Card } from "react-native-elements";



export default function SignUp({navigation,route}){   
   let [password,setPassword]=useState("")
   let [username,setUsername]=useState('')
   let[name,setName]=useState("")
   let[email,setEmail]=useState("")
   const [loggedin,setLoggedin]=useState(false)
   let [error,setError]=useState("")
   let signin=useCallback(()=>{
      console.log('update')
      if(password!="",username!="",name!="",email!=""){
         console.log('loggedin')

         setLoggedin(true)
      
         
      } else{
         console.log('false')
         setError("You need to enter Something")

      }
   },[
      password
   ])
   
   return(
   
      (loggedin)?  
      <View style={styles.view}>
           <Button onPress={()=>navigation.navigate("Home")} title="Log Out"></Button>

      </View >:
   <>
   <Card>
       
      <TextInput style={styles.input} onChangeText={(value)=>setName(value)} placeholder="Name"></TextInput> 
      <TextInput style={styles.input} onChangeText={(value)=>setUsername(value)} placeholder="Username"></TextInput> 
      <TextInput style={styles.input} onChangeText={(value)=>setPassword(value)} placeholder="Password"></TextInput> 
      <TextInput style={styles.input} onChangeText={(value)=>setEmail(value)} placeholder="Email"></TextInput> 
      <Text style={{color:'red'}}> {error}</Text>
      <Button title="Login" onPress={()=>signin(navigation.navigate("Todo"))}></Button>
   </Card>


      </>
   );
}


const styles = StyleSheet.create({
   view:{
      justifyContent: 'center',

   },


   input:{
      height:40,
      margin: 12,
      borderWidth:1,
      padding:10,
      backgroundColor:'#ADD8E6',
      textAlign: "left"


   },
   text: {
      fontWeight: "bold",
      fontSize: "1.5rem",
      marginVertical: "1em",
      textAlign: "center"
   }
});